.BlockFilms {
    position: relative;
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
    flex-grow: 1; /* BlockFilms занимает все доступное пространство */
    transition: transform 0.5s ease-in-out;
}
.slider_container { /* Новый контейнер */
    width:100vw;
    display: flex;
    height: 90%;
    overflow: hidden;
    flex-shrink: 1;
    align-items: center; /* Выравнивание по вертикали */
}
.button_next{
    background-image: url('https://avatars.mds.yandex.net/i?id=89f62cf2be242bb18ee5ee14fa1903cc_l-12190079-images-thumbs&n=13');
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;
    left: 92%;
    border-radius: 50%;
    z-index: 1;
    width: 50px;
    height: 50px;
}
.button_prev{ /* Стиль для кнопок */
    background-image: url('https://upload.wikimedia.org/wikipedia/commons/thumb/d/d3/Antu_system-log-out.svg/1200px-Antu_system-log-out.svg.png');
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;
    left: 26%;
    border-radius: 50%;
    z-index: 1;
    width: 50px;
    height: 50px;
}

