
.footer__logo{
    background-image: url('/public/black_on_white.png');
    background-size: contain;
    background-repeat: no-repeat;
    width: 175px;
    height: 150px;
}
.footer__nav{
    display:grid;
    grid-template-columns: repeat(3, 120px );
    grid-template-rows: repeat(4, 35px);
}
.Footer{
display:flex;
flex-direction: row;
height: 250px;
width:100%;
justify-content: space-evenly; 
border: 1px solid rgba(255, 255, 255, 0.466);
align-items: center;
background-color: rgb(40, 40, 40);
}
.footer__links{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 75px;
    width: 250px;
}
.footer__links a:hover{
    filter: brightness(0.7);
}
.footer__links img{
    object-fit: contain;
}
.footer__links_vk{
    background-image: url('https://upload.wikimedia.org/wikipedia/commons/2/21/VK.com-logo.svg');
    background-size: contain;
    background-repeat: no-repeat;
    width: 50px;
    height: 50px;
}
.footer__links_yt{
    background-image: url('https://cdn-icons-png.flaticon.com/512/1384/1384060.png');
    background-size: contain;
    background-repeat: no-repeat;
    width: 50px;
    height: 50px;
}
.footer__links_tg{
    background-image: url('https://pngicon.ru/file/uploads/telegram.png');
    background-size: contain;
    background-repeat: no-repeat;
    width: 50px;
    height: 50px;
}
.footer__links_obertka{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}
.footer__button{
     border-radius: 50%;
     z-index: 1;
     justify-content: center;
     align-items: center;
     display: flex;
    height: 40px;
    width: 100px;
}
.footer__links_obertka button:hover{
    filter: brightness(0.7);
}
