*{
     margin: 0; 
    padding: 0; 
    box-sizing: border-box; 
}
html {
    height: 100%;
    width:100%;
}
#root {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.Main{
    display: flex;
    flex-direction: column;
    height: 100%;
    width:70%;
    justify-content: center;
}
.Center{
    display: flex;
    width:100%;
    height: 500px;
}
.Global {
    display: flex;
    flex-direction: column;
    border: 1px solid rgba(255, 255, 255, 0.466);
    height: 100%;
    background-color:  rgba(19, 4, 4, 0.207);
    overflow: hidden;
}
.Name {
    display: flex;
    height: 10%;
    width: 100%;
    border: 1px solid rgba(255, 255, 255, 0.466);
}
.Film {
    display: flex;
    background-size: contain;
    background-repeat: no-repeat;
    width:143px;
    height:218px;
    border: 1px solid rgba(255, 255, 255, 0.466);
    transition: transform 0.5s ease-in-out;
}
.About {
    display: flex;
    align-items: center;
    border: 1px solid rgba(255, 255, 255, 0.466);
    background-color:  rgba(40, 40, 40, 0.207);
}
.obertka_about_info{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(40, 40, 40, 0.207);
    border: 1px solid rgba(255, 255, 255, 0.466);
    overflow: auto;
}
h1{
    color:rgba(161, 161, 161, 0.984);
    font-size: 18px;
    font-weight: bold;
    font-family: 'Arial', sans-serif;
    line-height: 1.6;
    margin-bottom: 1em;
    display:flex;
    justify-content: center;
}
a {
    color:rgba(161, 161, 161, 0.984);
    font-size: 14px;
    font-weight: bold;
    font-family: 'Arial', sans-serif;
    line-height: 1.6;
    margin-bottom: 1em;
    display:flex;
}
p{
    color:rgba(161, 161, 161, 0.984);
    display:flex;
    justify-content: center;
    align-items: center;
}
h4{
    color:rgba(29, 29, 29, 0.818);
}
.obertka_about_logo{
    display: flex;
    justify-content: start;
    width: 200px;
    height: 300px;
}
#about_logo {
    object-fit: contain;
}
