.navbar{
    display:flex;
    flex-direction: row;
    height: 75px;
    width:100%;
    justify-content: space-between;
    background-color:  rgb(40, 40, 40) ;
}
.centralblock{
    display: flex;
    align-items: end;
}
.find{
    display:flex;
    width: 100%;
    justify-content: end;
}
.logoname{
    display: flex;
    width:15%;
}
.join{
    width: 10%;
    display: flex;
}
.logoname img{
    object-fit:cover;
    width: 100%;
    height: 100%;
    filter: brightness(0.7);
}
.centralblock a{
    font-size: 28px;
    font-family:'Arial', sans-serif; 
    font-weight: bold;
    color: rgba(161, 161, 161, 0.984);
    text-decoration: none;
    display: flex;
    align-items: end;
}
.join a{
        font-size: 28px;
    font-family:'Arial', sans-serif; 
    font-weight: bold;
    color: rgba(161, 161, 161, 0.984);
    text-decoration: none;
    display: flex;
    align-items: end;
}